import React, { Component } from "react";
import { Modal, Button, Form, Col, DatePicker } from "antd";
import FloatLabel from "../form/FloatLabels";
import moment from "moment";
import "../modal/modal.scss";
import Grid from "@material-ui/core/Grid";
import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
} from "@material-ui/pickers";

let startDate = "";
class DatetimeModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
    };
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    this.props.onPublish();
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  disabledStartDate = (current) => {
    return current && current < moment().startOf("day");
  };

  disabledEndDate = (current) => {
    if (this.props.pollData.pollStartDate) {
      return current && current < moment(this.props.pollData.pollStartDate);
    } else {
      return current && current < moment().startOf("day");
    }
  };

  range = (start, end) => {
    const result = [];
    for (let i = start; i < end; i++) {
      result.push(i);
    }
    return result;
  };

  disabledDateTime = (current, type) => {
    if (type === "start") {
      startDate = current;
      if (moment(current).isSame(moment(), "day")) {
        var now = moment();
        return {
          disabledHours: () => this.range(0, now.hour()),
          disabledMinutes: () => this.range(0, now.minutes()),
          disabledSeconds: () => this.range(0, now.seconds()),
        };
      }
    } else if (type === "end") {
      if (moment(current).isSame(moment(), "day")) {
        var now = moment();
        return {
          disabledHours: () => this.range(0, now.hour()),
          disabledMinutes: () => this.range(0, now.minutes()),
          disabledSeconds: () => this.range(0, now.seconds()),
        };
      } else if (moment(current).isSame(moment(startDate))) {
        var now = moment(startDate);
        return {
          disabledHours: () => this.range(0, now.hour()),
          disabledMinutes: () => this.range(0, now.minutes()),
          disabledSeconds: () => this.range(0, now.seconds()),
        };
      }
    }
  };

  onChangeStartDate = (date) => {
    this.props.handlePollStartDatePickerChange(date, "pollStartDate");
  };

  onChangeEndDate = (date) => {
    this.props.handlePollEndDatePickerChange(date, "pollEndDate");
  };

  onChangeStartTime = (time) => {
    this.props.handlePollStartTimePickerChange(time, "pollStartTime");
  };

  onChangeEndTime = (time) => {
    this.props.handlePollEndTimePickerChange(time, "pollEndTime");
  };

  render() {
    return (
      <>
        <Button type="primary" className="md mr-4" onClick={this.showModal}>
          Schedule
        </Button>
        <Modal
          title="Publish Poll"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={520}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            className: "ant-btn-secondary",
            children: "Custom cancel",
          }}
          okText="Publish"
          cancelText="Cancel"
        >
          <Form className="input-md floating-label w-100">
            <Col span={24} className="gutter-row">
              <div className="mb-4 subtext-md">
                Select Date and Time to publish the
                <span className="pr-1 pr-1 font-weight-md6">
                  {" "}
                  {this.props.pollData.pollName}
                </span>
                Poll
              </div>

              <FloatLabel label="Select Start Date" value="sDate">
                <Form.Item
                  name="sDate"
                  rules={[
                    {
                      required: true,
                      message: "Please select date",
                    },
                  ]}
                >
                  <DatePicker
                    value={this.props.pollData.pollStartDate}
                    disabledDate={this.disabledStartDate}
                    onChange={(date) => this.onChangeStartDate(date)}
                    className="w-100"
                    name="sDate"
                    className="required"
                  />
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <Grid container justify="space-around">
                      <KeyboardTimePicker
                        margin="normal"
                        id="time-picker1"
                        label="Select Start Time"
                        value={this.props.pollData.pollStartTime}
                        onChange={(time) => this.onChangeStartTime(time)}
                        KeyboardButtonProps={{
                          "aria-label": "change time",
                        }}
                      />
                    </Grid>
                  </MuiPickersUtilsProvider>
                </Form.Item>
              </FloatLabel>
              <FloatLabel label="Select End Date" value="eDate">
                <Form.Item
                  name="eDate"
                  rules={[
                    {
                      required: true,
                      message: "Please select date",
                    },
                  ]}
                >
                  <DatePicker
                    value={this.props.pollData.pollEndDate}
                    disabledDate={this.disabledEndDate}
                    onChange={(date) => this.onChangeEndDate(date)}
                    className="w-100"
                    name="eDate"
                    className="required"
                  />
                </Form.Item>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <Grid container justify="space-around">
                    <KeyboardTimePicker
                      margin="normal"
                      id="time-picker2"
                      label="Select End Time"
                      value={this.props.pollData.pollEndTime}
                      onChange={(time) => this.onChangeEndTime(time)}
                      KeyboardButtonProps={{
                        "aria-label": "change time",
                      }}
                    />
                  </Grid>
                </MuiPickersUtilsProvider>
              </FloatLabel>
            </Col>
          </Form>
        </Modal>
      </>
    );
  }
}

export default DatetimeModal;
