import React, { Component } from "react";
import { Modal, Button } from "antd";
import "../modal/modal.scss";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import axios from "axios";

class DeleteModal extends Component {
  state = { visible: false, show: false };

  componentDidMount() {
    if (this.props.deleteOrg) {
      this.showModal();
    }
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  showBothModals = () => {
    this.setState({
      visible: true,
      show: true,
    });
  };

  handleOnOk = () => {
    this.setState({
      show: false,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    if (this.props.seededUser) {
      if (this.props.viaEdit) {
        this.props.handleDelete(this.props.user.email);
      } else {
        this.props.handleDelete(this.props.user.userId);
      }
    } else if (this.props.deleteOrg) {
      this.props.handleDelete(this.props.org);
    }
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
    if (this.props.deleteOrg) this.props.handleCancel();
  };

  deleteHotline() {
    const { history } = this.props;
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .delete(BASE_URL + "hotline/" + this.props.hotlineId, options)
      .then(() => {
        history.push("/orgcc/hotlinedirectory");
      })
      .catch((error) => {});
  }

  render() {
    return (
      <>
        {!this.props.deleteOrg ? (
          <Button onClick={this.showBothModals}>Delete</Button>
        ) : (
          <></>
        )}

        {this.props.deleteOrg ? (
          <Modal
            visible={this.state.visible}
            onOk={this.handleCancel}
            onCancel={this.handleOk}
            centered
            width={700}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{
              children: "Custom cancel",
            }}
            okText="Cancel"
            cancelText="Delete"
          >
            <p>
              You are about to Delete the Organization{" "}
              <strong>{this.props.org}</strong>. Deleting Organization would
              Delete all records associated with it. Are you sure you want to
              Delete Organization <strong>{this.props.org}</strong>?
            </p>
          </Modal>
        ) : !this.props.deleteOrg &&
          this.props.viaEdit &&
          this.props.seededUsers.length > 1 ? (
          <Modal
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            centered
            width={370}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{
              children: "Custom cancel",
            }}
            okText="Yes"
            cancelText="No"
          >
            <p>Are you sure you want to delete {this.props.user.firstName}?</p>
          </Modal>
        ) : !this.props.viaEdit && this.props.seededUser ? (
          <Modal
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            centered
            width={370}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{
              children: "Custom cancel",
            }}
            okText="Yes"
            cancelText="No"
          >
            <p>Are you sure you want to delete {this.props.user.firstName}?</p>
          </Modal>
        ) : !this.props.deleteOrg &&
          this.props.viaEdit &&
          this.props.seededUsers.length === 1 ? (
          <Modal
            visible={this.state.show}
            onOk={this.handleOnOk}
            centered
            width={370}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{ style: { display: "none" } }}
            okText="OK"
          >
            <p>There should be at least one seeded user.</p>
          </Modal>
        ) : (
          <Modal
            visible={this.state.show}
            onOk={this.handleOnOk}
            centered
            width={370}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{ style: { display: "none" } }}
            okText="OK"
          >
            <p>Are you sure you want to delete this?</p>
          </Modal>
        )}
      </>
    );
  }
}

export default DeleteModal;
