import React, { useState } from "react";
import {
  Row,
  Col,
  Form,
  Input,
  Button,
  DatePicker,
  Avatar,
  Divider,
  Select,
  Alert,
  Spin,
  Modal,
} from "antd";
import moment from "moment";
import FloatLabel from "./FloatLabels";
import AssignedUsers from "../manageOrg/AssignedUsers";
import "../form/form.scss";

const CreateOrgStepThree = ({
  prev,
  loader,
  apiError,
  handleOk,
  handleCancel,
  stepOneOrganizationInfo,
  handleStepOneOnChange,
  handleStepOneOnSelect,
  handleStepOneDatePickerChange,
  createNewOrganization,
  seededUsers,
  showEditSeededUserForm,
  deleteUser,
  addSeededUserFromThirdStep,
}) => {
  const [form] = Form.useForm();
  const { RangePicker } = DatePicker;
  const [error, showError] = useState(false);
  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const finish = (e) => {
    e.preventDefault();
    if (
      seededUsers &&
      stepOneOrganizationInfo.organizationName &&
      stepOneOrganizationInfo.organizationId &&
      stepOneOrganizationInfo.status1 &&
      stepOneOrganizationInfo.s1Date
    ) {
      showError(false);
      createNewOrganization();
    } else {
      showError(true);
    }
  };

  const stepThreePrev = (e) => {
    e.preventDefault();
    prev();
  };

  const handleEditUser = (userID) => {
    showEditSeededUserForm(userID, "viaStepThree");
  };

  const handleDelete = (userID) => {
    deleteUser(userID, "viaStepThree");
  };

  return (
    <>
      <h6 className="py-3 mb-0">Organization Details</h6>
      <div className="avatar-wrapper pt-2 pb-4">
        <Avatar>{stepOneOrganizationInfo.organizationName?.charAt(0)}</Avatar>
        <span className="md-name">
          {stepOneOrganizationInfo.organizationName}
        </span>
      </div>
      <Form
        className="input-md floating-label w-100"
        name="validate_other"
        form={form}
      >
        {error && (
          <Form.Item>
            <Alert
              message="Please fill the fields marked with asterisk(*)"
              type="error"
              showIcon
            ></Alert>
          </Form.Item>
        )}
        <Col span={24}>
          <Modal
            visible={apiError}
            onOk={handleOk}
            onCancel={handleCancel}
            centered
            width={500}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{ style: { display: "none" } }}
            okText="OK"
          >
            {" "}
            <p>There is some difficulty while creating this organization.</p>{" "}
          </Modal>
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label="Organization Name"
                value={stepOneOrganizationInfo.organizationName}
              >
                <Form.Item className="required" name="organizationName">
                  <Input
                    className="input-control"
                    htmlFor="organizationName"
                    id="organizationName"
                    defaultValue={stepOneOrganizationInfo.organizationName}
                    onChange={(e) => handleStepOneOnChange(e)}
                    value={stepOneOrganizationInfo.organizationName}
                    autoComplete="off"
                    disabled
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label="Organization Id"
                value={stepOneOrganizationInfo.organizationId}
              >
                <Form.Item
                  className="required"
                  name="organizationId"
                  validateStatus="success"
                >
                  <Input
                    className="input-control"
                    htmlFor="organizationId"
                    id="organizationId"
                    defaultValue={stepOneOrganizationInfo.organizationId}
                    onChange={(e) => handleStepOneOnChange(e)}
                    value={stepOneOrganizationInfo.organizationId}
                    autoComplete="off"
                    disabled
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label="Status"
                value={stepOneOrganizationInfo.status1}
              >
                <Form.Item className="required" name="status1">
                  <>
                    <Select
                      placeholder="Status"
                      className="input-control w-100 w-lg-100"
                      value={stepOneOrganizationInfo.status1}
                      htmlFor="status1"
                      onSelect={(value) =>
                        handleStepOneOnSelect(value, "status1")
                      }
                      disabled
                    >
                      <Option value="1">Active</Option>
                      <Option value="2">Inactive</Option>
                    </Select>
                  </>
                </Form.Item>
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel label="Select Date" value="date">
                <Form.Item className="input-control datepicker-wrapper required">
                  <RangePicker
                    className="w-100 "
                    name="s1Date"
                    defaultValue={stepOneOrganizationInfo.s1Date}
                    value={stepOneOrganizationInfo.s1Date}
                    onChange={(date) =>
                      handleStepOneDatePickerChange(date, "s1Date")
                    }
                    disabledDate={disabledDate}
                    disabled
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
          </Row>
        </Col>
        <Divider className="w-100" />
        <Spin spinning={loader} size="large">
          <Col
            span={24}
            className="d-flex justify-content-between align-items-center"
          >
            <h6 className="py-3 mb-0">Seeded Users Details </h6>
            <div>
              {" "}
              <Button
                className="mr-4"
                type="secondary"
                onClick={() => addSeededUserFromThirdStep()}
              >
                Add Seeded User
              </Button>
            </div>
          </Col>

          {/* <div className="avatar-wrapper pt-2 pb-4">
          <Avatar>{stepTwoOrganizationInfo.userName?.charAt(0)}</Avatar>
          <span className="md-name">{stepTwoOrganizationInfo.userName}</span>
        </div> */}
          <Col span={24}>
            <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
              <AssignedUsers
                seededUsers={seededUsers}
                handleEditUser={handleEditUser}
                handleDelete={handleDelete}
                fromStepThree
              />
            </Row>
          </Col>
          <Form.Item>
            <div className="steps-action mt-5">
              <Button className="mr-4" type="secondary" onClick={stepThreePrev}>
                Back
              </Button>
              <Button type="primary" htmlType="submit" onClick={finish}>
                Create and Invite
              </Button>
            </div>
          </Form.Item>
        </Spin>
      </Form>
    </>
  );
};

export default CreateOrgStepThree;
