import React from "react";
import { Table, Button, Row, Col, Alert } from "antd";
import AssignHotlineModal from "../modal/AssignHotlineModal";
import { getManageEmpAsync } from "../../actions/manageEmp";
import Search from "antd/lib/input/Search";
import { connect } from "react-redux";
import "../table/table.scss";

class CreateOpenHotlineStepTwo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      employeeData: "",
      showAssigntoModal: "false",
      showAlert: false,
    };
  }

  componentDidMount() {
    const { dispatch } = this.props;
    dispatch(getManageEmpAsync());
  }

  componentDidUpdate(prevProps) {
    let { manageEmpData } = this.props;
    if (prevProps.manageEmpData != manageEmpData) {
      this.setState({
        employeeData: manageEmpData,
      }, ()=>{
        this.onSearch("");
      });
    }
  }

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  onSearch = (value) => {
    const { employeeData } = this.state;
    const filterDt = this.filterIt(employeeData, value);
    this.setState({ filterData: filterDt });
  };

  start = () => {
    this.setState({
      showAssigntoModal: true,
    });
  };

  stepTwoPrev = () => {
    this.props.prev();
  };

  stepTwoNext = () => {
    if (this.props.stepTwoOpenLine.chekedValues.length !== 0) {
      this.props.next();
    } else {
      this.setState({ showAlert: true });
    }
  };

  render() {
    const { selectedRowKeys } = this.props.stepTwoOpenLine;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.props.handleStepTwoOnSelect,
    };
    
    const columns = [
      {
        title: "First Name",
        dataIndex: "firstname",
        key: "firstname",
        width: "22%",
      },
      {
        title: "Last Name",
        dataIndex: "lastname",
        key: "lastname",
        width: "22%",
      },
      {
        title: "Mobile Number",
        dataIndex: "Mobile_Number",
        key: "Mobile_Number",
        width: "21%",
      },
      {
        title: "Access",
        dataIndex: "Access",
        render: (res) => {
          let test = "";
          for (let property in res) {
            if (res[property] == true) {
              if (property == "am_web") {
                property = "AM WEB";
              }
              if (property == "org_cc_user") {
                property = "Org CC";
              }
              if (property == "app_user") {
                property = "APP";
              }

              if (test.length == 0) test += property;
              else test += ", " + property;
              //test += property + " ";
            }
          }
          return <span>{test}</span>;
        },
        width: "20%",
      },
      {
        title: "Status",
        dataIndex: "",
        key: "x",
        width: "10%",
        // render: () => (
        //   <div className="primary-tag-rounded tag">
        //     <span className="circle mr-1"></span>Active
        //   </div>
        // ),
        render: () => (
          <div className="primary-tag-rounded tag">
            <span className="circle mr-1"></span>Active
          </div>
        ),
      },
    ];
    return (
      <div>
        <Col data-test="createHotlineStepTwo" span={24}>
          <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
          <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
              <Row>
              <Col span={24} lg={9} className="search-box">

                  <Search placeholder="Search User" onSearch={this.onSearch} ></Search>
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
        {this.state.showAlert && (
          <Alert
            message="Please select user(s) and assign required permission(s) using Assign button."
            type="error"
            showIcon
          />
        )}
        <div id="no-more-tables">
        <Table
          rowSelection={rowSelection}
          columns={columns}
          dataSource={this.state.filterData}
          rowKey="unique_identifier"
        />
        </div>

        <div className="steps-action mt-5">
          <Button
            className="mr-4"
            type="secondary"
            style={{ margin: "0 8px" }}
            onClick={() => this.stepTwoPrev()}
            data-test="steptwoPrev"
          >
            Back
          </Button>
          <Button
            className="md"
            type="primary"
            onClick={() => this.stepTwoNext()}
            data-test="steptwoNext"
          >
            Next
          </Button>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { manageEmpData } = state.manageEmp;
  const { message } = state.message;
  return {
    manageEmpData,
    message,
  };
}
export default connect(mapStateToProps)(CreateOpenHotlineStepTwo);
