import React, { Component } from "react";
import { Modal, Button, Input, Row, Form } from "antd";
import "../modal/modal.scss";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { BASE_URL } from "../../userconfig";
import * as AppStorage from '../../utils/AppStorage';

var CryptoJS = require("crypto-js");
const { TextArea } = Input;
class NoteModal extends React.Component {
  constructor(props) {
    super(props);
    this.formRef = React.createRef();
    this.state={
      visible: false, 
      noteText:""
    } 
  }
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });

    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));

    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    var data = {
      note_text: this.state.noteText,
      conversation_id: this.props.convoId,
      created_by:  parseInt(
        CryptoJS.AES.decrypt(
          user.user_details.org_user_id,
          "anonymoose"
        ).toString(CryptoJS.enc.Utf8)
      ),
    };

    axios
      .post(BASE_URL + "addNotes/", data, options)
      .then((res) => {
        if (res.status === 200) {
          this.setState({noteText:""})
          this.formRef.current.resetFields()
          this.props.getNoteData(res.data.conversation_id);
        }
      });

  };

  handleCancel = () => {
    this.setState({
      visible: false,noteText:""
    });
    this.formRef.current.resetFields();
  };

   handleChange = (e) => {
    this.setState({  noteText: e.target.value });
  };

  render() {
    return (
      <>
        <Button onClick={this.showModal} type="link" className="link pl-0 pt-0">
          <span className="icon icon-add-filled" />
          Take a note..
        </Button>
        <Modal
          className="note-modal"
          title="Take a Note"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={500}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            children: "Custom cancel",
            className: "ant-btn-secondary",
          }}
          okText="Add"
          cancelText="Cancel"
        >
          <Row className="pt-2" data-test="NoteModal">
          <Form  className="input-md w-100" name="validate-other" ref={this.formRef}>
              <Form.Item className="mb-0" name="send message">
                <TextArea
                  className="input-control"
                  placeholder="Add note here..."
                  htmlFor="send message"
                  value={this.state.noteText}
                  onChange={(e) => this.handleChange(e)}
                />
              </Form.Item>
            </Form>
          </Row>
        </Modal>
      </>
    );
  }
}

export default NoteModal;