import React, { Component } from "react";
import { Modal, Button, Row, Tooltip } from "antd";
import "../modal/modal.scss";

class AddIcoModal extends Component {
  state = { visible: false, iconContent: "", ImageId: "" };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    this.props.addIcon(this.state.iconContent, this.state.ImageId);
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  onSelectIcon = (e) => {
    this.setState({
      iconContent: e.currentTarget.innerHTML,
      ImageId: e.currentTarget.id,
    });
  };

  render() {
    return (
      <> 
        <Button type="link" className="link pl-0" onClick={this.showModal}>
          <span className="icon icon-add-filled" />
          Add Icon
        </Button>
        <Modal
          className="modal-icon"
          title="Add Icon"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={500}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            children: "Custom cancel",
            className: "ant-btn-secondary",
          }}
          okText="Add"
          cancelText="Cancel"
        >
          <Row className="py-4" data-test="AddIcon">
            <div className={this.state.ImageId === '1' ? "col-3 col-lg-2 selected" : "col-3 col-lg-2"} id={1} onClick={this.onSelectIcon}>
              <Tooltip title="Sexual Harrasment">
                <span className="icon icon-sexual-harassment-1" />
              </Tooltip>
            </div>

            <div className={this.state.ImageId === '2' ? "col-3 col-lg-2 selected" : "col-3 col-lg-2"} id={2} onClick={this.onSelectIcon}>
              <Tooltip title="Violence">
                <span className="icon icon-violence" />
              </Tooltip>
            </div>
            <div className={this.state.ImageId === '3' ? "col-3 col-lg-2 selected" : "col-3 col-lg-2"} id={3} onClick={this.onSelectIcon}>
              <Tooltip title="Racism">
                <span className="icon icon-racism" />
              </Tooltip>
            </div>
            <div className={this.state.ImageId === '4' ? "col-3 col-lg-2 selected" : "col-3 col-lg-2"} id={4} onClick={this.onSelectIcon}>
              <Tooltip title="Cyber Bullying">
                <span className="icon icon-cyber-bullying" />
              </Tooltip>
            </div>
            <div className={this.state.ImageId === '5' ? "col-3 col-lg-2 selected" : "col-3 col-lg-2"} id={5} onClick={this.onSelectIcon}>
              <Tooltip title="Fight">
                <span className="icon icon-fight" />
              </Tooltip>
            </div>
            <div className={this.state.ImageId === '6' ? "col-3 col-lg-2 selected" : "col-3 col-lg-2"} id={6} onClick={this.onSelectIcon}>
              <Tooltip title="Discrimination">
                <span className="icon icon-discrimination" />
              </Tooltip>
            </div>
            <div className={this.state.ImageId === '7' ? "col-3 col-lg-2 selected" : "col-3 col-lg-2"} id={7} onClick={this.onSelectIcon}>
              <Tooltip title="Slap">
                <span className="icon icon-slap" />
              </Tooltip>
            </div>
          </Row>
        </Modal>
      </>
    );
  }
}

export default AddIcoModal;
