import React, { Component } from "react";
import { Typography } from "antd";
import moment from "moment"

const { Paragraph } = Typography;

class AddedNotes extends Component {
  state = {
    expand: false,
    counter: 0,
    notesData:[],
    conversationId:""
  };

  typoExpand = () => {
    this.setState({
      expand: true,
      counter: !this.state.expand
        ? this.state.counter + 0
        : this.state.counter + 1,
    });
  };

  typoClose = () => {
    this.setState({
      expand: false,
      counter: !this.state.expand
        ? this.state.counter + 0
        : this.state.counter + 1,
    });
  };

  renderParagraph(item) {
    return (
      <div className="note-wrapper m-2" key={this.state.counter} >
        <span className="note-user">{moment(item.created_on).utc().local().format("MM:DD:YY • hh:mm A •")} {item.first_name} {item.last_name}</span>
        <Paragraph
          ellipsis={{
            rows: 3,
            expandable: true,
            symbol: "Show more",
            onExpand: this.typoExpand,
          }}
        >
         {item.note_text}
        </Paragraph>
      </div>
    );
  }

  render() {
    return (
     this.props.addedNote.map((item)=>(
      <div className="show-less" data-test="addedNotes">
        {this.renderParagraph(item) }
        {this.state.expand && (
          <a data-test="showLess" className="d-flex justify-content-end" onClick={this.typoClose}>
            Show less
          </a>
        )}
       </div>
     ))
    );
  }
}

export default AddedNotes;