import React from "react";
import {
  Row,
  Col,
  Input,
  Avatar,
  Form,
  Select,
  Modal,
  Alert,
  DatePicker,
} from "antd";
import FloatLabel from "./FloatLabels";
import "react-phone-input-2/lib/style.css";
import PhoneInput from "react-phone-input-2";
import ResetPwdModal from "../modal/ResetPwdModal";

const { RangePicker } = DatePicker;

const EditSeededUser = (props) => {
  return (
    <Modal
      visible={true}
      onOk={props.handleEditOk}
      onCancel={props.handleEditCancel}
      centered
      width={900}
      okButtonProps={{
        children: "Custom OK",
      }}
      cancelButtonProps={{
        children: "Custom cancel",
      }}
      okText="Update"
      cancelText="Cancel"
    >
      <Form className="input-md floating-label w-100" name="validate_other">
        <div>Edit Seeded User</div>
        <br />
        <div className="avatar-wrapper pt-2 pb-4">
          <Avatar>{props.seededUserName.charAt(0)}</Avatar>
          <span className="md-name">{props.seededUserName}</span>
        </div>
        {props.error && (
          <Form.Item>
            <Alert
              message="Please fill the fields marked with asterisk(*)"
              type="error"
              showIcon
            ></Alert>
          </Form.Item>
        )}
        <Col span={24}>
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label="First Name"
                value={props.seededUserDetails.firstName}
              >
                <Form.Item name="firstName" className="required">
                  <Input
                    id="firstName"
                    className="input-control"
                    defaultValue={props.seededUserDetails.firstName}
                    htmlFor="firstName"
                    value={props.seededUserDetails.firstName}
                    onChange={(e) => props.handleSeededUserOnChange(e)}
                    autoComplete="off"
                    maxLength={50}
                  />
                </Form.Item>
                {props.fNameCount && (
                  <Form.Item>
                    <Alert
                      message="Max Characters limit reached."
                      type="error"
                    ></Alert>
                  </Form.Item>
                )}
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label="Last Name"
                value={props.seededUserDetails.lastName}
              >
                <Form.Item name="lastName" className="required">
                  <Input
                    className="input-control"
                    id="lastName"
                    htmlFor="lastName"
                    className="required"
                    defaultValue={props.seededUserDetails.lastName}
                    value={props.seededUserDetails.lastName}
                    onChange={(e) => props.handleSeededUserOnChange(e)}
                    autoComplete="off"
                    maxLength={50}
                  />
                </Form.Item>
                {props.lNameCount && (
                  <Form.Item>
                    <Alert
                      message="Max Characters limit reached."
                      type="error"
                    ></Alert>
                  </Form.Item>
                )}
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label="Email Id"
                value={props.seededUserDetails.email}
              >
                <Form.Item name="email" className="required">
                  <Input
                    className="input-control"
                    id="email"
                    htmlFor="email"
                    className="required"
                    defaultValue={props.seededUserDetails.email}
                    value={props.seededUserDetails.email}
                    onChange={(e) => props.handleSeededUserOnChange(e)}
                    onBlur={(e) => props.validateEmailAddress(e)}
                    autoComplete="off"
                    disabled
                  />
                  {props.emailValidationError && (
                    <Alert
                      message="Please enter a valid email address."
                      type="error"
                      showIcon
                    ></Alert>
                  )}
                </Form.Item>
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel label="Date" value="date2">
                <Form.Item className="input-control datepicker-wrapper required">
                  <RangePicker
                    className="new w-100"
                    name="date2"
                    className="required"
                    value={props.seededUserDetails.date2}
                    onChange={(date) =>
                      props.handleSeededUserDatePickerChange(date, "date2")
                    }
                    disabledDate={props.disabledDate}
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row phone-field">
              <FloatLabel label="Mobile" value={props.seededUserDetails.mobile}>
                <Form.Item name="mobile" className="required">
                  <PhoneInput
                    country={"in"}
                    id="mobile"
                    htmlFor="mobile"
                    defaultValue={props.seededUserDetails.mobile}
                    value={props.seededUserDetails.mobile}
                    onChange={props.handleMobile}
                    onBlur={(e) => props.validateMobile(e)}
                    autoComplete="off"
                  />
                  {props.mobileValidationError && (
                    <Alert
                      message="Please enter a valid mobile number."
                      type="error"
                      showIcon
                    ></Alert>
                  )}
                </Form.Item>
              </FloatLabel>
            </Col>
            <Col span={24} md={8} className="gutter-row">
              <FloatLabel
                label="Status"
                value={props.seededUserDetails.status2}
              >
                <Form.Item className="required" name="status2">
                  <>
                    <Select
                      className="input-control w-100 w-lg-50"
                      value={props.seededUserDetails.status2}
                      htmlFor="status2"
                      className="required"
                      onSelect={(value) =>
                        props.handleSeededUserOnSelect(value, "status2")
                      }
                    >
                      <Option value="1">Active</Option>
                      <Option value="2">Inactive</Option>
                    </Select>
                  </>
                </Form.Item>
              </FloatLabel>
            </Col>
          </Row>
        </Col>
      </Form>
      <ResetPwdModal
        user={props.seededUserDetails.firstName}
        handleResetPasswordOk={props.handleResetPasswordOk}
        handleResetPasswordCancel={props.handleResetPasswordCancel}
      />
    </Modal>
  );
};

export default EditSeededUser;
