import React from "react";
import{Row, Col, Spin} from "antd";

const Spinner = (loader) => (
  <Col
    span={24}
    lg={18}
    className="chat-section no-selected bg-white justify-content-center align-items-center d-none d-lg-flex"
  >
    <Row className="justify-content-center my-3">
      <Spin size="large" tip="Loading..." spinning={loader}></Spin>
    </Row>
  </Col>
);

export default Spinner;
