import React, { Component } from "react";
import { Modal, Button, Form, Col, Input } from "antd";
import FloatLabel from "../form/FloatLabels";
import "../modal/modal.scss";

class ResetPwdModal extends Component {
  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    this.props.handleResetPasswordOk();
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
    this.props.handleResetPasswordCancel();
  };

  render() {
    return (
      <>
        <Button type="link" className="link pl-0 mt-4" onClick={this.showModal}>
          <span>Reset password for this user</span>
        </Button>
        <Modal
          title="Reset Password"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={370}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            className: "ant-btn-secondary",
            children: "Custom cancel",
          }}
          okText="Reset and Invite"
          cancelText="Cancel"
        >
          <Form className="input-md floating-label w-100">
            <Col span={24} className="gutter-row">
              <div className="mb-4 subtext-md">
                Reset password for{" "}
                <span className="font-weight-md">{this.props.user}</span>
              </div>
              <FloatLabel label="New Password" value="">
                <Form.Item
                  name="newpwd"
                  className="pwd"
                  rules={[
                    {
                      required: true,
                      message: "Please enter your new password!",
                    },
                  ]}
                >
                  <Input.Password
                    className="input-control"
                    htmlFor="newpwd"
                    className="required"
                    value=""
                  />
                </Form.Item>
              </FloatLabel>
              <FloatLabel label="Confirm Password" value="">
                <Form.Item
                  name="confirmpwd"
                  className="pwd"
                  rules={[
                    {
                      required: true,
                      message: "Please enter your confirmed password!",
                    },
                  ]}
                >
                  <Input.Password
                    className="input-control"
                    htmlFor="confirmpwd"
                    className="required"
                    value=""
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
          </Form>
        </Modal>
      </>
    );
  }
}

export default ResetPwdModal;
