import React from "react";
import {
  Form,
  Select,
  Input,
  Button,
  DatePicker,
} from "antd";
import "../form/form.scss";
import ReactHtmlParser from "react-html-parser";
const { TextArea } = Input;

const CreateHotlineStepThree = ({
  prev,
  createNewHotLine,
  stepOneHotLine,
  stepTwoHotLine,
  getIconText
}) => {
  
  const [form] = Form.useForm();
  const stepThreeBack = (e) => {
    e.preventDefault();
    prev();
  };
  const generateHotLine = (e) => {
    createNewHotLine();
  };

  const assignedUserNames = stepTwoHotLine.selectedRows.map(
    (element) => element.firstname + " " + element.lastname
  );

  const { RangePicker } = DatePicker;

  return (
    <Form data-test="createHotlineStepThree" className="input-md w-md-100 w-75" name="validate_other" form={form}>
      <Form.Item name="directoryName">
        <>
          <TextArea
            showCount
            disabled={true}
            maxLength={80}
            value={stepOneHotLine.directoryName}
            id={"directoryName"}
            className="input-control textarea-input"
            placeholder="Directory Name"
            htmlFor="directoryName"
          />
        </>
      </Form.Item>
      <Form.Item name="description">
        <>
          <TextArea
            disabled={true}
            className="input-control textarea-md"
            value={stepOneHotLine.description}
            id={"description"}
            placeholder="Description"
            htmlFor="description"
          />
        </>
      </Form.Item>

      <Form.Item className="input-control datepicker-wrapper">
        <>
          <RangePicker
            className="datepicker w-100 w-lg-50 mr-0 mr-lg-5"
            name="sdate"
            value={stepOneHotLine.sDate}
            rules={[
              {
                readOnly: true,
                required: true,
                message: "Please select start date",
              },
            ]}
            disabled
          />
        </>
        {/* <DatePicker className="datepicker w-50"
          name="tDate"
          value={hotlineForm.tDate}
          rules={[
            {

              required: true,
              message: "Please select End Date date",
            },
          ]}
          //  onChange={(date, dateString) => handleDatePickerChange(date, dateString,'tDate')}
          disabled /> */}
      </Form.Item>
      <div className="d-block d-lg-flex">
        <div className="w-100 w-lg-50 mr-0 mr-lg-5">
          <Form.Item name="status">
            <>
              <Select
                className="input-control"
                placeholder="Status"
                value={stepOneHotLine.status}
                htmlFor="status"
                disabled={true}
              >
                <Select.Option value="5">Active</Select.Option>
                <Select.Option value="4">Inactive</Select.Option>{" "}
              </Select>
            </>
          </Form.Item>
        </div>
        <div className="w-100 w-lg-50 mr-0 mr-lg-5">
          <Form.Item name="userNames">
            <>
              <Input
                className="input-control"
                placeholder="Assigned to"
                htmlFor="assigned to"
                id={assignedUserNames}
                disabled
                value={assignedUserNames.join(", ")}
              />
            </>
          </Form.Item>
        </div>
      </div>
      <Form.Item className="w-100 w-lg-50">
        <Form.Item className="upload-control">
          <div className="ant-upload-list ant-upload-list-picture">
            <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
              <div className="ant-upload-list-item-info d-inline-flex w-100">
                <span className="d-inline-flex align-items-center">
                  <a className="ant-upload-list-item-thumbnail" href="#" rel="">
                    {ReactHtmlParser(stepOneHotLine.iconHtml)}
                  </a>

                  <span
                    className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                    title="women safety"
                  >
                    {getIconText(stepOneHotLine.hotlineIconId)}
                  </span>
                </span>
              </div>
            </div>
          </div>
        </Form.Item>
      </Form.Item>

      <div className="steps-action mt-5">
        <Button
          type="secondary"
          className="mr-4"
          type="secondary"
          style={{ margin: "0 8px" }}
          htmlType="submit"
          onClick={stepThreeBack}
          data-test="stepThreePrev"
        >
          Back
        </Button>
        <Button
          type="primary"
          className="md"
          htmlType="submit"
          onClick={generateHotLine}
          data-test="stepThreeNext"
        >
          Create
        </Button>
      </div>
    </Form>
  );
};

export default CreateHotlineStepThree;
